<template>
  <div class="login-container">
    <div class="login-card">
      <div class="login-header">
        <h2>信息管理系统</h2>
        <p>欢迎登录</p>
      </div>
      
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        class="login-form"
      >
        <el-form-item>
          <el-input
            v-model="loginForm.username"
            placeholder="请输入用户名"
            size="large"
            prefix-icon="User"
            clearable
          />
        </el-form-item>
        
        <el-form-item>
          <el-input
            v-model="loginForm.password"
            type="password"
            placeholder="请输入密码"
            size="large"
            prefix-icon="Lock"
            show-password
            clearable
          />
        </el-form-item>
        
        <el-form-item>
          <el-button
            type="primary"
            size="large"
            class="login-btn"
            @click="handleLogin"
          >
            登录
          </el-button>
        </el-form-item>
        
        <div class="login-footer">
          <router-link to="/register">还没有账号？立即注册</router-link>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { useAuthStore } from '@/stores/auth'
import { API_CONFIG } from '@/config/api'

const router = useRouter()
const authStore = useAuthStore()

const loginFormRef = ref()
const loginForm = reactive({
  username: '',
  password: ''
})

const handleLogin = async () => {
  if (!loginForm.username || !loginForm.password) {
    ElMessage.warning('请输入用户名和密码')
    return
  }
  
  console.log('=== 登录调试信息 ===')
  console.log('用户名:', loginForm.username)
  console.log('密码:', loginForm.password) // 临时显示密码用于调试
  console.log('API配置:', API_CONFIG)
  
  try {
    console.log('开始调用authStore.login...')
    await authStore.login({
      username: loginForm.username,
      password: loginForm.password
    })
    
    console.log('authStore.login调用成功')
    console.log('认证状态:', authStore.isAuthenticated)
    console.log('token:', authStore.token)
    console.log('用户信息:', authStore.user)
    
    // 只有真正成功才显示成功消息
    if (authStore.isAuthenticated && authStore.token) {
      ElMessage.success('登录成功')
      console.log('准备跳转到首页...')
      await router.push('/')
      console.log('跳转完成')
    } else {
      console.error('登录状态异常：没有获得token或用户信息')
      ElMessage.error('登录状态异常，请重试')
    }
  } catch (error: any) {
    console.error('=== 登录错误详情 ===')
    console.error('错误对象:', error)
    console.error('错误消息:', error.message)
    console.error('错误响应:', error.response?.data)
    console.error('错误状态码:', error.response?.status)
    
    // 不显示成功消息，错误消息已经在拦截器中处理
  }
}
</script>

<style scoped>
.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.login-card {
  width: 400px;
  padding: 40px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.login-header {
  text-align: center;
  margin-bottom: 30px;
}

.login-header h2 {
  color: #303133;
  margin-bottom: 8px;
  font-size: 24px;
  font-weight: 600;
}

.login-header p {
  color: #909399;
  font-size: 14px;
}

.login-btn {
  width: 100%;
  margin-top: 10px;
}

.login-footer {
  text-align: center;
  margin-top: 20px;
}

.login-footer a {
  color: #409eff;
  text-decoration: none;
  font-size: 14px;
}
</style>